const initialState = {
    sharedVariable: '',
  };
  
  const rootReducer = (state = initialState, action) => {
    switch (action.type) {
      case 'SET_SHARED_VARIABLE':
        return {
          ...state,
          sharedVariable: action.payload,
        };
      default:
        return state;
    }
  };
  
  export default rootReducer;